.mode-dropdown {
  padding: 5px 15px;
  margin-bottom: 150px;
  border: transparent;
  border-radius: 5px;
}

.mode-dropdown:focus {
  outline: #4b82a6;
}

.button {
  padding: 5px 10px;
  margin: 5px 10px;
  border: transparent;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  color: #fefae0;
  background-color: #bc6c25;
}